import React from "react";
import s from "./ProfileInfo.module.css";
import Photo from "../../../assets/images/sky.png";
import userPhoto from "../../../assets/images/pngegg.png";
import Preloader from "../../common/Preloader/Pleloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";

const ProfileInfo = ({ profile, status, updateStatus, isOwner }) => {
  //debugger;
  if (!profile) {
    return <Preloader />;
  }

  return (
    <div>
      <div className={s.item}>
        <img alt="phon" src={Photo} />
      </div>

      <div className={s.descriptionBlock}>
        <div>{profile.userId}</div>
        <div>
          <img
            className={s.mainPhoto}
            alt="photos"
            src={
              profile.photos.large != null ? profile.photos.large : userPhoto
            }
          />
          {isOwner && <input type={"file"}/>}
        </div>

        <ProfileStatusWithHooks status={status} updateStatus={updateStatus} />

        <div>{profile.fullName}</div>
        <div>{profile.aboutMe}</div>
        <div>{profile.lookingForAJobDescription}</div>
        <div>{profile.contacts.instagram}</div>
        <div>{profile.contacts.facebook}</div>
        <div>{profile.contacts.twitter}</div>
      </div>
    </div>
  );
};

export default ProfileInfo;
