import React from "react";
import s from "./Post.module.css";
import userPhoto from "../../../../assets/images/gerl.png";

const Post = (props) => {
  return (
    <div className={s.item}>
      <img alt="" src={userPhoto} />
      {props.messege}
      <div>
        <span>{props.likesCount}</span>
      </div>
    </div>
  );
};

export default Post;
